

.container {
    position: relative;
    top: 400px;
    margin: 0;
    height: 100px;
    opacity: 1;
    top: 300px;
    
}

.title {
    color:white;
    text-align: center;
    padding: 20px 0 0 0;
    font-size: 4em;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    position: relative;
    margin: 0;
    opacity: 1;
    top: -10px;
  
}

#titleheading {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #1C4772;
    text-align: center;
    font-size: 3em;
    opacity: 1;
   

}
.text {
    color:white;
    text-align: center;
    padding: 20px, 20px, 20px, 20px; 
    font-size: 2em;
    
}
.code {
    position: static;
    height: 50px;
}
   

body {
    background-image: url("./Images/poppyback.JPG");
    background-size: 1400px;
    width: 100%;
    background-repeat: no-repeat;
    overflow: hidden;
    opacity: 1;
}
.imgetitle{
    font-size: 0.8em;
   padding-left: 50px;
   text-decoration: underline;
}
@media only screen and (max-width: 600px) {
    .coverpic{
        height: 100px;
    }
    .text {
        display: inline;
    }
    .title {
        display: inherit;
    }
    body {
        display: block;
    }
  }